import React from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';

class GetCapsules extends React.Component {
    constructor(props) {
        super(props);
        this.state = {phone: "",
                      amount: 0,
                      offset: 0,
                      isWatched: false}
    }


    send = () => {
        axios.get('https://capsules-web-server.herokuapp.com/capsules',
        {
            params: {
                phone: this.state.phone,
                amount: this.state.amount,
                offset: this.state.offset,
                isWatched: this.state.isWatched
            }
        })
        .then(res => {
          console.log(res)
        })
    }
    
    render() {
        return (
            <div>
                <span>Get capsules:</span>
                <input onChange={(e) => this.setState({phone: e.target.value})} type='text' placeholder='phone'></input>
                <input onChange={(e) => this.setState({amount: +e.target.value})} type='text' placeholder='amount'></input>
                <input onChange={(e) => this.setState({offset: +e.target.value})} type='text' placeholder='offset'></input>
                <input onChange={(e) => this.setState({isWatched: e.target.value === 'true'})} type='text' placeholder='isWatched'></input>
                <button onClick={this.send}>send</button>
            </div>
        );
    }
}

ReactDOM.render(<GetCapsules/>, document.getElementById('root'));